:root {
  --main-color: #e98b2c;
  --dark: #1b1b22;
  --dark-light: #32323a;
  --white: #fff;
  --white-light: #c8c8c8;
}

body {
  color: #c8c8c8;
  color: var(--white-light);
  background: #1b1b22;
  background: var(--dark);
  font-family: 'Nunito', sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

nav {
  background: var(--dark-light);
  display: flex;
  align-items: center;
  justify-content: center;
}

.form__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 40px;
}

.alert {
  padding: 10px;
  color: white;
  font-size: 20px;
  margin-bottom: 3px;
  display: flex;
  align-items: center;
  border-radius: 4px;
  background-color: #db6262;
  -webkit-animation: fade 3.5s linear infinite;
          animation: fade 3.5s linear infinite;
}

.alert i {
  font-size: 15px;
  cursor: pointer;
  background-color: var(--dark-light);
  padding: 5px;
  border-radius: 4px;
  margin-right: 5px;
}

.added {
  padding: 10px;
  color: white;
  font-size: 20px;
  margin-bottom: 3px;
  display: flex;
  align-items: center;
  border-radius: 4px;
  background-color: #5e9f5e;
  -webkit-animation: fade 2s forwards;
          animation: fade 2s forwards;
}

.added i {
  font-size: 15px;
  cursor: pointer;
  background-color: var(--dark-light);
  padding: 5px;
  border-radius: 4px;
  margin-right: 5px;
}

@-webkit-keyframes fade {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes fade {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

form .input {
  margin: 0 10px 0;
  display: flex;
  align-items: center;
}

form .input label {
  background: var(--dark-light);
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
}

form .input input {
  width: 300px;
  padding: 10px;
  border: 0;
  outline: none;
  font-size: 17px;
}

.btn_1 {
  padding: 10px 0;
  margin-left: 50px;
}

.btn_1 button {
  border: 0;
  outline: 0;
  margin-right: 10px;
  padding: 10px 20px;
  border-radius: 6px;
  color: var(--white);
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn_1 button.avilable {
  background: #428b42;
}

.btn_1 button.avilable:hover {
  background: #166916;
}

.btn_1 button.not {
  background: #f33f3f;
}

.btn_1 button.not:hover {
  background: #d81818;
}

.add {
  margin-top: 10px;
  width: 300px;
  padding: 10px 20px;
  border-radius: 11px;
  border: 0;
  outline: 0;
  color: var(--white);
  background: #4267b2;
  cursor: pointer;
  font-size: 17px;
  font-weight: bolder;
  transition: 0.3s;
  margin-left: 30px;
}

.add:hover {
  background: #2a53a5;
}

.table_container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 20%;
}

.table__btn {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.refresh-btn {
  background: var(--dark-light);
  color: var(--white);
  border: 0;
  outline: 0;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  letter-spacing: 1.5px;
}

